@import "../../../data";

.DialogItem {

  a {
    display: flex;
    align-items: center;
    padding: 1em;
    cursor: pointer;
    background: lighten($gray-color, 18);
    border-right: 1px solid $main-color;

    &:hover {
      background: lighten($gray-color, 19);
    }
  }
}
.active {
  font-weight: bold;
  color: $main-color;
  background: lighten($gray-color, 16) !important;
  border: 1px solid $main-color;
  border-right: 1px solid lighten($gray-color, 16) !important;
}
.Icon {
  width: 1em;
  height: 1em;
  margin-right: .5em;
  fill: $main-color;
}
