@import "../../data";

.dialogsWrapper {
  display: grid;
  grid-template-columns: 1fr 5fr;
}

.dialogs {
  position: relative;
  padding: 0;
  margin: 0 -1px 0 0;
  list-style: none;
}

.messages {
  padding: 1em;
  //border-left: 0;
  border: 1px solid $main-color;
  background: lighten($gray-color, 16%);
}

.new {
  margin: 1em -1em -1em;
  padding: 1.5em 2em .25em;
  background: lighten($gray-color, 15%);
}
.messagesWarning {
  display: flex;
  align-items: center;
  color: $warning-color;
}
.messagesIcon {
  width: 2em;
  height: 2em;
  margin-right: .5em;
  fill: $warning-color;
}
.Form {
  .Row {
    margin: 0 0 1em;

    &.TextRight {
      text-align: right;
    }
  }
  .Label {
    display: block;
    margin-bottom: .35em;
    font-size: .9em;
  }
  .CheckboxLabel {
    font-size: .9em;
    margin-left: .35em;
  }

  .Input {
    padding: .5em 1em;
    border: 0;
    border-radius: .25em;
    box-shadow: 0 0 0 1px $gray-color;
  }


  .Textarea {
    width: 94%;
    padding: .5em 1em;
    border: 0;
    border-radius: .25em;
    box-shadow: 0 0 0 1px $gray-color;
  }

  .Button {
    padding: .5em 1em;
    border: 0;
    border-radius: 3px;
    color: rgb(255, 255, 255);
    background: $main-color;
    cursor: pointer;
    outline: none;

    &:hover {
      background: darken($main-color, 10);
    }
  }
}
