@import "../../../data";

.message {
  padding: 0 .5em 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.myMessage {
  padding: 0 1em 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row-reverse;
}
.author {
  display: block;
  width: 48px;
  height: 48px;
  line-height: 48px;
  margin: 0 .5em;
  border-radius: 50%;
  border: 1px solid lighten($gray-color, 10);
  background: lighten($gray-color, 15);
  text-align: center;
}
.text {
  display: block;
  flex: auto;
  margin: 0 .5em;
  padding: 1em;
  background: #fff;
  border-radius: .5em;
}
